.new-request {
    width: 100%;
    background-color: black;
    color: white;
    display: flex;
}
.request,
.response,
.msg
{
    width: 50vw;
    height: 100vh;
    padding: 10px;
}
.request > div,
.response > div
{
    width: 100%;
    margin: 15px 0;
}
.request {
    border-right: 1px solid white;
}
.request-head {
    width: 100%;
    display: flex;
    align-items: center;
}
.method select,
.code select,
.url input,
.send {
    padding: 10px 12px;
}

.url,
.url input {
    width: 100%;
}

.send {
    background-color: #3F51B5;
    cursor: pointer;
}
.send:active{
    opacity: 0.8;
}
.meta {
    width: 100%;
    background-color: black;
    font-weight: 600;
    padding: 10px;
    color: white;
}

.meta span {
    color: green;
    padding-right: 20px;
}

.body-head {
    display: flex;
    background-color: burlywood;
}

.body-head>div {
    padding: 8px;
    margin: 5px 10px;
    text-align: center;
    cursor: pointer;
    font-weight: 600;
    color: black;
}

.body-head .set {
    border-bottom: 2px solid blue;
}

.soon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 200px;
    font-weight: 600;
}
.req-body textarea{
    width: 100%;
    height: 62vh;
    background-color: black;
    color: white;
    resize: none;
}
.res {
    color: white;
    font-size: 20px;
}
.select-code{
    display: flex;
    justify-content: space-around;
    padding: 10px 0;
}
pre {
    font-size: 16px;
    background-color: black;
    color: white;
    white-space: pre-wrap;
    display: block;
}
.msg{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}